class Toss {
    DEFAULT_TYPE = 'cheese'
    DEFAULT_AMOUNT = 'regular'
    DEFAULT_HALF = 'full'
    DEFAULT_SAUCE = 'butter'
    type = null
    amount = null
    half = null
    sauce = null
    cost = null

    TYPES = [ ['cheese', 2], ['carnivore', 4], ['herbivore', 4], ['omnivore', 6]]
    AMOUNT = ['regular', 'light', 'extra'] 
    HALF = ['full', 'halfCheese', 'halfCarni', 'halfHerb', 'halfOmni']
    SAUCES = ['butter', 'ranch', 'hot']


    constructor(type, amount, half, sauce, cost){
        this.type = type ? type : this.DEFAULT_TYPE
        this.amount = amount ? amount : this.DEFAULT_AMOUNT
        this.half = half ? half : this.DEFAULT_HALF
        this.sauce = sauce ? sauce : this.DEFAULT_SAUCE
        this.cost = cost

        this.bake()
    }

    bake(){
        this.pi = []
        this.pi.push (new Pizza(this.type, this.amount, this.half, this.sauce, this.cost))
    }

}